<template>
  <div class="main_dispatch">
    <div id="dispatchWrapper">
      <div class="filterlogoDispatch">
        <div id="filter_dispatch_wrapper">
          <div id="filter_dispatch_section">
            <!-- @update:checked="this.handleUpdateSelect()" -->
            <n-checkbox v-model:checked="showDispatch[0]" @update:checked="updateQuery()">Normal</n-checkbox>
            <n-checkbox v-model:checked="showDispatch[1]" @update:checked="updateQuery()">Special</n-checkbox>
            <n-checkbox v-model:checked="showDispatch[2]" @update:checked="updateQuery()">Millionaire</n-checkbox>
          </div>
          <div id="filter_dispatch_select">
            <n-select
              v-model:value="filterDispatch[0]"
              filterable
              placeholder="Quest Stage"
              :options=dispatchInfo.stage
              style="width: 130px;"
              clearable
              :consistent-menu-width="false"
              @update:value="updateQuery()"
            />
            <n-select
              v-model:value="filterDispatch[1]"
              filterable
              placeholder="Quest Owner"
              :options=dispatchInfo.char
              style="width: 130px;"
              clearable
              :consistent-menu-width="false"
              @update:value="updateQuery()"
            />
          </div>
          {{selectedDispatch}}
        </div>
        <div id="disable_images">
          <n-button type="warning" @click="toggleDisable()">
            <span v-if="screenWidth >= 425">
              {{ disableText }}
            </span>
            <span v-else>
              {{ disableTextTiny }}
            </span>
          </n-button>
        </div>
        <!-- <p id="filtertext">Select an adventurer</p> -->
      </div>
      <div id="filter_listadv">
          <input v-model="filterText" type="text" placeholder="Put an adventurer name or title..." @input="updateQuery()" maxlength="30">
          <button id="loadfilterButton_dispatch" @click="searchFilter()">Load</button>
        </div>
      <div class="char_page" v-if="filteredDispatch.length != 0">
        <n-grid :x-gap="0" :y-gap="0" :cols="1" :style="{'padding-top': '8px', 'padding-bottom': '8px'}">
          <n-grid-item v-for="disp in filteredDispatch" :key="disp.id_quest" :id="disp.id_quest">
            <n-card :id="screenWidth >= 768 ? 'cell_dispatch' : 'cell_dispatch_mobile'" :style="cardStyle(disp.quest_type, disp.id_quest)" 
              @click="screenWidth >= 768 ? clickDispatch(disp.id_quest) : '0'"  >
              <div v-if="screenWidth >= 768">
                <span id="questname"> {{ disp.quest_name}} </span>
                <span id="queststage"> {{ disp.quest_level }}-{{ disp.quest_stage }} </span>
                <div v-if="disableImages"> <!--Mostrar imagem-->
                  <div id="quest_owner_text">
                    <!-- <img :alt="disp['quest_who']" :title="disp['quest_who']" :src="getImage(disp['quest_who'])"> -->
                  </div>
                  <div id="quest_characters">
                    <div v-for="char in 4" :key="char" id="quest_characters_cell">
                      <!-- <img :alt="disp['quest_unit_' + (char)]" :title="disp['quest_unit_' + (char)]" :src="getImage(disp['quest_unit_' + (char)])"> -->
                    </div>
                  </div>
                </div>
                <div v-else>
                  <div id="quest_owner_text">
                    <span>
                      {{ disp.quest_who }}
                    </span>
                  </div>
                  <div id="quest_characters">
                    <div id="quest_characters_noImage">
                      <div v-for="char in 4" :key="char" id="quest_characters_cell_noImage">
                        <span>
                          {{ disp['quest_unit_' + (char)] }}
                        </span>
                      </div>
                    </div>
                  </div>
                </div>
                <span id="hint">Hint: {{ disp.quest_text }}</span>
              </div>
              <div v-else>
                <span id="questname_mobile"> {{ disp.quest_name}} </span>
                <span id="queststage_mobile"> {{ disp.quest_level }}-{{ disp.quest_stage }} </span>
                <div v-if="disableImages"> <!--Mostrar imagem-->
                  <div id="quest_owner_mobile_text">
                    <!-- <img :alt="disp['quest_who']" :title="disp['quest_who']" :src="getImage(disp['quest_who'])"> -->
                  </div>
                  <div id="quest_characters_mobile">
                    <div v-for="char in 4" :key="char" id="quest_characters_mobile_cell">
                      <!-- <img :alt="disp['quest_unit_' + (char)]" :title="disp['quest_unit_' + (char)]" :src="getImage(disp['quest_unit_' + (char)])"> -->
                    </div>
                  </div>
                </div>
                <div v-else>
                  <div id="quest_owner_mobile_text">
                      <span>
                        {{ disp.quest_who }}
                      </span>
                  </div>
                  <div id="quest_characters_mobile">
                    <div id="quest_characters_mobile_noImage">
                      <div v-for="char in 4" :key="char" id="quest_characters_mobile_cell_noImage">
                        <span>
                          {{ disp['quest_unit_' + (char)] }}
                        </span>
                      </div>
                    </div>
                  </div>
                </div>
                <span id="hint_mobile">Hint: {{ disp.quest_text }}</span>
              </div>
            </n-card>
          </n-grid-item>
        </n-grid>
      </div>
      <div class="char_page" v-else>
        <div id="errorPage">
          <n-result status="error" title="No Quest Found" description="Maybe change the filter options...? If not, contact the creator.">
          </n-result>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { 
  NGrid, 
  NGridItem, 
  NButton,
  NSpace,
  NModal,
  NCard,
  NCheckbox,
  NSelect,
  NResult
} from 'naive-ui'
import { ref } from '@vue/reactivity'

export default {
  props: ["id"],
  data() {
    return {
      screenWidth: window.innerWidth,
      
      // showModal: ref(false),
      selectedDispatch: null,

      // Type Search
      showDispatch: [true, true, true],
      // Stage Filter
      filterDispatch: ref([null, null]),
      // Stage Text
      filterText: ref(null),

      disableImages: true,
      disableText: "Disable Images",
      disableTextTiny: "D",


      dispatchData: [],
      filteredDispatch: [],

      dispatchInfo: null
    }
  },
  components:{
    NGrid, 
    NGridItem, 
    NButton,
    NSpace,
    NModal,
    NCard,
    NCheckbox,
    NSelect,
    NResult
  },
  beforeMount() {
    var dispatch = require('../composables/constants/dispatchNormal.json')
    var info = require('../composables/constants/dispatch_info.json')

    let query = this.$route.query
    if(Object.keys(query).length != 0){
      this.showDispatch[0] = query.type != null ? (query.type.indexOf("n") > -1 ? true : false) : true
      this.showDispatch[1] = query.type != null ? (query.type.indexOf("s") > -1 ? true : false) : true
      this.showDispatch[2] = query.type != null ? (query.type.indexOf("m") > -1 ? true : false) : true
      this.filterDispatch[0] = query.stage
      this.filterDispatch[1] = query.owner
      this.filterText = query.text
    }

    this.dispatchData = dispatch
    this.dispatchInfo = info
    // this.filteredDispatch = dispatch
    this.searchFilter()

    this.onResize()
  },
  mounted() {
    this.$nextTick(() => {
      window.addEventListener('resize', this.onResize);
    })
  },
  beforeDestroy() { 
    window.removeEventListener('resize', this.onResize); 
  },
  methods: {
    onResize() {
      this.screenWidth = window.innerWidth
    },
    goCombination(combination_skill){
      let skill = combination_skill.toLowerCase().replace(/ /g, "_")
      this.$router.push({ name: 'filter_adv', query: { comb_skill: skill }});
    },
    getImage(url){
      let temp = url.toLowerCase().replace(/ /g, "_")
      // console.log(temp)
      return require('../assets/dispatch/portrait/portrait_' + temp + '.png')
    },
    updateQuery(){
      let show = (this.showDispatch[0] ? "n" : "") + (this.showDispatch[1] ? "s" : "") + (this.showDispatch[2] ? "m" : "")
      let filter = {
        type: show.length == 3 ? null : show,
        stage: this.filterDispatch[0],
        owner: this.filterDispatch[1],
        text: this.filterText.length == 0 ? null : this.filterText,
      }

      Object.keys(filter).forEach(key => {
        if (filter[key] === null || filter[key] === undefined) {
          delete filter[key];
        }
      });
      this.$router.replace({ name: 'dispatch', query: filter })
      this.searchFilter()
    },
    clickDispatch(i){
      this.selectedDispatch = i
      this.showModal = true
    },
    toggleDisable(){
      if(this.disableImages){
        this.disableImages = !this.disableImages
        this.disableText = "Enable Images"
        this.disableTextTiny = "E"
      }else{
        this.disableImages = !this.disableImages
        this.disableText = "Disable Images"
        this.disableTextTiny = "D"
      }
    },
    cardStyle(questType, idquest){
      let style = {};
      if(this.screenWidth >= 768){
        switch(questType){
          case "normal":
            style["border"] = '2px solid';
            style["border-image"] = 'linear-gradient(120deg, rgba(167,155,107,1) 0%, rgba(145,129,77,1) 100%) 1';
            style["border-bottom"] = 'none';
            break;
          case "special":
            style["border"] = '2px solid';
            style["border-image"] = 'linear-gradient(285deg, rgba(0,105,181,1) 0%, rgba(0,212,255,1) 100%) 1';
            style["border-bottom"] = 'none';
            break;
          case "millionaire":
            style["border"] = '2px solid';
            style["border-image"] = 'linear-gradient(68deg, rgba(255,220,2,1) 0%, rgba(248,191,51,1) 100%) 1';
            style["border-bottom"] = 'none';
            break;
        }
        if(idquest == "36" || idquest == "40" || idquest == "47" ){
            style['border-bottom'] = '3px solid';
            style['margin-bottom'] = '16px';
        }
        if(this.filteredDispatch[this.filteredDispatch.length - 1].id_quest == idquest){
          style['border-bottom'] = '2px solid';
          style['margin-bottom'] = '5px';
        }
      }else{
        switch(questType){
          case "normal":
            style["border"] = '2px solid';
            style["border-image"] = 'linear-gradient(120deg, rgba(167,155,107,1) 0%, rgba(145,129,77,1) 100%) 1';
            style["border-bottom"] = 'none';
            break;
          case "special":
            style["border"] = '2px solid';
            style["border-image"] = 'linear-gradient(285deg, rgba(0,105,181,1) 0%, rgba(0,212,255,1) 100%) 1';
            style["border-bottom"] = 'none';
            break;
          case "millionaire":
            style["border"] = '2px solid';
            style["border-image"] = 'linear-gradient(68deg, rgba(255,220,2,1) 0%, rgba(248,191,51,1) 100%) 1';
            style["border-bottom"] = 'none';
            break;
        }
        if(idquest == "36" || idquest == "40" || idquest == "47" ){
            style['border-bottom'] = '2px solid';
            style['margin-bottom'] = '5px';
        }
        if(this.filteredDispatch[this.filteredDispatch.length - 1].id_quest == idquest){
          style['border-bottom'] = '2px solid';
          style['margin-bottom'] = '5px';
        }
      }
      return style
    },
    searchFilter(){

      let tempData = this.dispatchData.filter((quest) => {
        let temp = true;
        switch(quest.quest_type){
          case "normal":
            if(this.showDispatch[0]){
              temp &&= true
            }else{
              temp &&= false
            }
            break;
          case "special":
            if(this.showDispatch[1]){
              temp &&= true
            }else{
              temp &&= false
            }
            break;
          case "millionaire":
            if(this.showDispatch[2]){
              temp &&= true
            }else{
              temp &&= false
            }
            break;
        }
        if(this.filterDispatch[0]){
          if(quest.quest_level_id == this.filterDispatch[0]){
            temp &&= true
          }else{
            temp &&= false
          }
        }
        if(this.filterDispatch[1]){
          if(quest.quest_who.toLowerCase().replace(" ", "_") == this.filterDispatch[1]){
            temp &&= true
          }else{
            temp &&= false
          }
        }

        let temp2 = false
        if(!(this.filterText == null || this.filterText == null!= "")){
          temp2 ||= quest.quest_who.toLowerCase().indexOf(this.filterText.toLowerCase()) > -1 ? true : false
          temp2 ||= quest.quest_unit_1.toLowerCase().indexOf(this.filterText.toLowerCase()) > -1 ? true : false
          temp2 ||= quest.quest_unit_2.toLowerCase().indexOf(this.filterText.toLowerCase()) > -1 ? true : false
          temp2 ||= quest.quest_unit_3.toLowerCase().indexOf(this.filterText.toLowerCase()) > -1 ? true : false
          temp2 ||= quest.quest_unit_4.toLowerCase().indexOf(this.filterText.toLowerCase()) > -1 ? true : false
          temp2 ||= quest.quest_name.toLowerCase().indexOf(this.filterText.toLowerCase()) > -1 ? true : false
          temp2 ||= quest.quest_level.toLowerCase().indexOf(this.filterText.toLowerCase()) > -1 ? true : false
          temp &&= temp2
        }
      
        return temp
      })
      this.filteredDispatch = tempData
    }
  },
}
</script>

